export default {
	data() {
		return {
			input:'',//用户输入运单号
			inputoff:false,//控制input输入显示
			paging:0,//控制顶部页面选项
			scrollLeft: 0,
			isClickChange: false,
			currentTab0: 0,
			currentTab: 0,
			menuTabs0: [{name: '我的进货单'},
				{name: '我的提货单'},
				{name: '我的下级进货单'},
				{name: '我的下级提货单'}
			],
			menuTabs: [{name: '全部'},
				{name: '未发货'},
				{name: '已发货'},
				{name: '交易完成'},
				{name: '异常关闭'}
			],
			menuLists: [],//加载订单
			userlist: '' //用户缓存
		};
	},
	onLoad() {
		this.userlist = uni.getStorageSync('userlist'); //加载用户缓存
		console.log(this.userlist);
		this.judgeOrder();
	},
	methods: {
		//查看订单
		Orderdetails(item){
			console.log(item);
			this.doUrl('pages/index/orderDetails',item);
		},
		// 更新用户信息
		updateUser: function(data) {
			// 判断是否登录
			this.utils.showloading();
			this.http
				.getApi('/user/login', { storeLogin: 1, storeOpendid: data }, 'post', '')
				.then(res => {
					uni.setStorageSync('userlist', res.data);
					this.judgeOrder();
					this.utils.success('刷新成功！',()=>{
						uni.hideLoading();
					});
				})
				.catch(err => {
				});
		},
		// 确认收货
		Confirm(e){
			// let e = e;
			this.utils.showloading();
			this.http.getApi('/yunorder/takeOver', { orderNo: e }, 'get', this.userlist.storeOpendid)
			.then(res => {
				// this.menuLists = res.data.list;
				console.log(res);
				this.utils.success('操作成功！',()=>{
					uni.hideLoading();
					this.updateUser(this.userlist.storeOpendid);
				});
			})
			.catch(err => {
				uni.hideLoading();
				console.log(err);
			});
		},
		onKeyInput: function(event) {
		    this.input = event.target.value
			console.log(this.input);
		},
		delivery(){
			this.inputoff = true;
			// let e = e;
			uni.showModal({
			    title: '请填写快递运单号',
			    content: '请在输入框处填写快递运单号',
			    success: function (res) {
			        if (res.confirm) {
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
			
		},
		delivery2(e){
			this.utils.showloading();
			console.log('fsnfklsfs'+this.input);
			this.http.getApi('/order/delivery', {orderNo:e, wuliuNo: this.input }, 'get', this.userlist.storeOpendid)
			.then(res => {
				// this.menuLists = res.data.list;
				console.log(res);
				this.utils.success('操作成功！',()=>{
					uni.hideLoading();
					this.inputoff = false;
					this.updateUser(this.userlist.storeOpendid);
				})
			})
			.catch(err => {
				uni.hideLoading();
				console.log(err);
				this.utils.success('该订单已操作成功！',()=>{
					uni.hideLoading();
					this.inputoff = false;
					this.updateUser(this.userlist.storeOpendid);
				})
			});
		},
		
		swichMenu: async function(current) {
			//点击其中一个选项
			if (this.currentTab == current) {
				return false;
			} else {
				if (current == 0) {
					this.currentTab = current;
					this.judgeOrder();
				} else {
					this.currentTab = current;
					this.abnormalOrder(current);
				}
			}
		},
		purchase(e){
			//点击其中一个选项
			if (this.paging == e) {
				return false;
			} else {
				if (e == 0) {
					this.paging = e;
					this.judgeOrder();
				} else {
					this.paging = e;
					this.abnormalOrder(e);
				}
			}
		},
		// 获取全部进订单
		judgeOrder() {
			this.utils.showloading();
			if (this.paging==0) {
				this.http.getApi('/yunorder/selectMyAll', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					this.menuLists = res.data.list;
					console.log(res);
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
			}
			if (this.paging==1) {
				this.http.getApi('/yunorder/selectMyAlltifyingTwo', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					this.menuLists = res.data.list;
					console.log(res);
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
			}
			if (this.paging==2) {
				this.http.getApi('/yunorder/getorder', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					this.menuLists = res.data.list;
					console.log(res);
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
			}
			else{
				this.http.getApi('/yunorder/getorderTwo', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
				.then(res => {
					uni.hideLoading();
					this.menuLists = res.data.list;
					console.log(res);
				})
				.catch(err => {
					uni.hideLoading();
					console.log(err);
				});
			}
			
		},
		//获取其他订单
		abnormalOrder(current) {
			let com = current;
			this.utils.showloading();
			if (this.paging==0) {
				// this.http.getApi('/order/getmyorderStusas', { myId: this.userlist.id, status: com - 1, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
				// .then(res => {
				// 	uni.hideLoading();
				// 	this.menuLists = res.data.list;
				// 	console.log(res);
				// })
				// .catch(err => {
				// 	uni.hideLoading();
				// 	console.log(err);
				// });
				if(current==1){
					this.http.getApi('/yunorder/statusZero', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						this.menuLists = res.data.list;
						console.log(res);
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
				}
				if(current==2){
					this.http.getApi('/yunorder/statusOne', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						this.menuLists = res.data.list;
						console.log(res);
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
				}
				if(current==3){
					this.http.getApi('/yunorder/statusOk', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						this.menuLists = res.data.list;
						console.log(res);
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
				}
				else{
					this.menuLists=null;
					this.utils.success('查询成功！',()=>{
						uni.hideLoading();
					})
				}
			} 
			if (this.paging==1) {
				if(current==1){
					this.http.getApi('/yunorder/statusZerotifyingTwo', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						this.menuLists = res.data.list;
						console.log(res);
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
				}
				if(current==2){
					this.http.getApi('/yunorder/statusOnetifyingTwo', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						this.menuLists = res.data.list;
						console.log(res);
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
				}
				if(current==3){
					this.http.getApi('/yunorder/statusOktifyingTwo', { uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						this.menuLists = res.data.list;
						console.log(res);
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
				}
				else{
					this.menuLists=null;
					this.utils.success('查询成功！',()=>{
						uni.hideLoading();
					})
				}
			}
			if (this.paging==2) {
				if(current!=0){
					this.http.getApi('/yunorder/fifyOne', { status:com-1, uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
					.then(res => {
						uni.hideLoading();
						this.menuLists = res.data.list;
						console.log(res);
					})
					.catch(err => {
						uni.hideLoading();
						console.log(err);
					});
				}
				else{
					// this.paging = current;
					// this.judgeOrder();
					this.menuLists=null;
					this.utils.success('查询成功！',()=>{
						uni.hideLoading();
					})
				}
			}
			else{
				if (this.paging==3) {
					if(current!=0){
						this.http.getApi('/yunorder/fifyTow', { status:com-1, uid: this.userlist.id, pageNum: 1, pageSize: 20 }, 'get', this.userlist.storeOpendid)
						.then(res => {
							uni.hideLoading();
							this.menuLists = res.data.list;
							console.log(res);
						})
						.catch(err => {
							uni.hideLoading();
							console.log(err);
						});
					}
					else{
						// this.paging = current;
						// this.judgeOrder();
						this.menuLists=null;
						this.utils.success('查询成功！',()=>{
							uni.hideLoading();
						})
					}
				}
				 else{
					this.menuLists=null;
					this.utils.success('暂无订单！',()=>{
						uni.hideLoading();
					})
				}
					
				
				
			}
		},
		
	}
};
